import { lazy, Suspense } from "preact/compat";
import type { CoordinatorCardView as _CoordinatorCardView } from "../components/coordinator/CoordinatorCardView";
import type { CoordinatorTableView as _CoordinatorTableView } from "../components/coordinator/CoordinatorTableView";
import type { CoordinatorPage as _CoordinatorPage } from "./Coordinator";

type Params<T> = T extends (...args: [infer U]) => any ? U : Record<string, never>;

const Suspender = <T extends (...args: any) => any>(Comp: T) => {
	const Suspended = (props: Params<T>) => (
		<Suspense fallback>
			<Comp {...props} />
		</Suspense>
	);
	return Suspended;
};

const defaultLoader =
	<T extends Record<string, any>>(defaultExport: keyof T) =>
	(mod: T) =>
		mod[defaultExport];

// Pages

/** Lazy export of {@link _CoordinatorPage | CoordinatorPage} */
export const CoordinatorPage = Suspender(lazy(() => import("./Coordinator").then(defaultLoader("CoordinatorPage"))));

// Components

/** Lazy export of {@link _CoordinatorCardView | CoordinatorCardView} */
export const CoordinatorCardView = Suspender(
	lazy(() => import("../components/coordinator/CoordinatorCardView").then(defaultLoader("CoordinatorCardView")))
);

/** Lazy export of {@link _CoordinatorTableView | CoordinatorTableView} */
export const CoordinatorTableView = Suspender(
	lazy(() => import("../components/coordinator/CoordinatorTableView").then(defaultLoader("CoordinatorTableView")))
);
